import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/study',
    name: 'study',
    meta:{display:true},
    component: () => import(/* webpackChunkName: "about" */ '../views/Study.vue')
  },
  {
    path: '/center',
    name: 'center',
    meta: { display:true },
    component: () => import(/* webpackChunkName: "about" */ '../views/Center.vue')
  },
  {
    path: '/show',
    name: 'show',
    component: () => import(/* webpackChunkName: "about" */ '../views/Show.vue')
  },
  {
    path: '/admin',
    name: 'admin',
    meta: { display:true },
    component: () => import(/* webpackChunkName: "about" */ '../views/Admin.vue')
  },
  {
    path:'/login',
    name:'login',
    meta: { display:true,tabber:true },
    component: () => import('../views/login/login.vue')
  },
  {
    path:'/message',
    name:'message',
    meta:{display:true,tabber:true},
    component: () => import('../views/message/Message.vue')
  },{
    path:'/article/:article',
    name:'article',
    meta:{tabber:true},
    component: () => import('../views/Article.vue')
  },{
    path:'/collect',
    name:'collect',
    meta:{tabber:true},
    component: () => import('../views/Collect.vue')
  },{
    path:'/school',
    name:'school',
    meta:{tabber:true},
    component: () => import('../views/center/school.vue')
  },{
    path:'/news',
    name:'news',
    meta:{tabber:true},
    component: () => import('../views/center/news.vue')
  },{
    path:'/application',
    name:'application',
    meta:{tabber:true},
    component: () => import('../views/center/application.vue')
  },{
    path:'/secondaryCollege',
    name:'secondaryCollege',
    meta:{tabber:true},
    component: () => import('../views/center/secondaryCollege.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
