<script>
import card from '@/components/card'
import Title from '../components/my-title.vue';
export default {
  components: {
    card, Title
  },
  data() {
    return {
      title: [
        {
          message: '校园安全教育',
          right: '进入学习'
        },
        {
          message: '微视频教育',
          right: '观看视频'
        },
      ],
      cardList: [
        {
          img: require('../assets/img/study/用电安全.jpg'),
          title: '用电安全',
        },
        {
          img: require('../assets/img/study/网络隐私安全.jpg'),
          title: '隐私安全',
        },
        {
          img: require('../assets/img/study/心理健康.jpg'),
          title: '心理安全',
        },
        {
          img: require('../assets/img/study/校园贷.jpg'),
          title: '校园贷',
        },
      ],
      images: [
        require('../assets/img/home/swiper1.png'),
        require('../assets/img/home/swiper2.jpg'),
        require('../assets/img/home/swiper3.jpg'),
        require('../assets/img/home/swiper4.jpg'),
        require('../assets/img/home/swiper5.jpg'),
      ],
      flag: false,
    };
  },
  methods: {
    change(index) {
      this.flag = false
      this.play(index)
    },
    play(index) {
      let video = document.getElementsByTagName('video')
      for (let i = 0; i < video.length; i++) {
        video[i].pause()
      }
      this.flag = !this.flag
      if (this.flag) {
        video[index].play()
      } else {
        video[index].pause()
      }
    }
  }
};
</script>

