import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    collectList:[],
    clickCount:148318
  },
  mutations: {
    AddClickCount(state,value){
      state.clickCount+=value
    }
  },
  actions: {
    AddClickCount(context,value){
      context.commit('AddClickCount',value)
    }
  },
  modules: {
  },
  getters:{
    getClickCount:(state)=>state.clickCount
  }
})
